.sidebar {
  background-color: var(--c-bkg-sidebar);
  border-right: 1px solid var(--c-border);
  bottom: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  transition: all 0.2s ease;
  width: 280px;
  z-index: 1000;
}
.sidebar ul {
  list-style-type: none;
}
@media screen and (min-width: 992px) and (max-width: 1439px) {
  .sidebar {
    width: 70px;
  }
  .sidebar .sidebar-inner .sidebar-logo {
    border-bottom: 1px solid transparent;
    padding: 0;
  }
  .sidebar .sidebar-inner .sidebar-logo a .logo {
    background-position: center center;
    width: 70px;
  }
  .sidebar .sidebar-inner .sidebar-menu {
    overflow-x: hidden;
  }
  .sidebar .sidebar-inner .sidebar-menu > li > a .title {
    display: none;
  }
  .sidebar .sidebar-inner .sidebar-menu li.dropdown .arrow {
    opacity: 0;
  }
  .sidebar .sidebar-inner .sidebar-menu li.dropdown.open ul.dropdown-menu {
    display: none !important;
  }
  .sidebar:hover {
    width: 280px;
  }
  .sidebar:hover .sidebar-inner .sidebar-logo {
    border-bottom: 1px solid #e6ecf5;
    padding: 0 20px;
  }
  .sidebar:hover .sidebar-inner .sidebar-menu > li > a .title {
    display: inline-block;
  }
  .sidebar:hover .sidebar-inner .sidebar-menu li.dropdown .arrow {
    opacity: 1;
  }
  .sidebar:hover .sidebar-inner .sidebar-menu li.open > ul.dropdown-menu {
    display: block !important;
  }
}
@media screen and (max-width: 991px) {
  .sidebar {
    left: -280px;
    width: calc(280px - 30px);
  }
}

.sidebar-inner {
  position: relative;
  height: 100%;
}

.sidebar-logo {
  border-bottom: 1px solid var(--c-border);
  line-height: 0;
  padding: 0 20px;
  transition: all 0.2s ease;
  background: var(--c-bkg-card);
}
.sidebar-logo a {
  display: inline-block;
  width: 100%;
}
.sidebar-logo a .logo {
  background-position: center left;
  background-repeat: no-repeat;
  display: inline-block;
  min-height: calc(65px - 1px);
  width: 100%;
  width: 70px;
}
.sidebar-logo a .logo img {
  height: 42px;
  width: auto;
  display: block;
  margin: 11px auto;
}
.sidebar-logo a .logo-text {
  color: var(--c-text-base);
}
.sidebar-logo .mobile-toggle {
  display: none;
  float: right;
  font-size: 18px;
  line-height: calc(65px - 1px);
}
.sidebar-logo .mobile-toggle a {
  color: var(--c-icon);
}
.sidebar-logo .mobile-toggle a:hover {
  color: var(--c-icon-hover);
}
@media screen and (max-width: 991px) {
  .sidebar-logo .mobile-toggle {
    display: inline-block;
  }
}
@media screen and (min-width: 992px) and (max-width: 1439px) {
  .sidebar-logo .mobile-toggle {
    display: none;
  }
}

.sidebar-menu::before, .sidebar-menu::after {
  content: " ";
  display: table;
}
.sidebar-menu::after {
  clear: both;
}
.sidebar-menu {
  height: calc(100vh - 65px);
  list-style: none;
  margin: 0;
  overflow: auto;
  padding: 0.75rem 0;
  position: relative;
}
.sidebar-menu .dropdown-toggle::after {
  display: none;
}
.sidebar-menu .sidebar-link.actived::before {
  background: #2196f3;
  border-radius: 50%;
  content: "";
  display: block;
  height: 8px;
  left: -4px;
  position: absolute;
  top: calc(50% - 4px);
  width: 8px;
}
.sidebar-menu li {
  position: relative;
}
.sidebar-menu li.dropdown .arrow {
  font-size: 10px;
  line-height: 40px;
  position: absolute;
  right: 30px;
  transition: all 0.05s ease-in;
}
@media screen and (max-width: 991px) {
  .sidebar-menu li.dropdown .arrow {
    right: 25px;
