     }\n\n        > .dropdown-menu {\n          display: block;\n\n          .dropdown-menu {\n            padding-left: 20px;\n          }\n\n          .arrow {\n            line-height: 25px;\n          }\n        }\n      }\n    }\n\n    a {\n      color: var(--c-text-muted);\n      transition: all 0.3s ease;\n\n      &:hover,\n      &:focus {\n        color: var(--c-text-base);\n        text-decoration: none;\n\n        .icon-holder {\n          color: var(--c-icon-hover);\n        }\n      }\n    }\n  }\n\n  > li {\n    &.dropdown {\n      ul {\n        &.dropdown-menu {\n          background-color: transparent;\n          border-radius: 0;\n          border: 0;\n          box-shadow: none;\n          float: none;\n          padding-left: 50px;\n          padding-top: 0;\n          position: relative;\n          width: 100%;\n\n          > li {\n            > a {\n              display: block;\n              padding: 10px 15px;\n\n              &:hover,\n              &:focus {\n                background-color: transparent;\n                color: var(--c-text-base);\n              }\n            }\n\n            &.actived {\n              a {\n                color: var(--c-text-base);\n              }\n            }\n          }\n        }\n      }\n    }\n\n    > a {\n      display: block;\n      font-size: 15px;\n      font-weight: 500;\n      padding: 5px 15px;\n      position: relative;\n      white-space: nowrap;\n\n      .icon-holder {\n        border-radius: 6px;\n        color: var(--c-icon);\n        display: inline-block;\n        font-size: 17px;\n        height: 35px;\n        left: 0;\n        line-height: 35px;\n        margin-right: 14px;\n        position: relative;\n        text-align: center;\n        transition: all 0.3s ease;\n        width: 35px;\n      }\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @Sidebar Collapsed\n// ---------------------------------------------------------\n\n.is-collapsed {\n  .sidebar {\n    @include from($breakpoint-xl) {\n      width: $collapsed-size;\n\n      .sidebar-inner {\n        .sidebar-logo {\n          border-bottom: 1px solid transparent;\n          padding: 0;\n        }\n\n        .sidebar-menu {\n          overflow-x: hidden;\n\n          > li {\n            > a {\n              .title {\n                display: none;\n              }\n            }\n          }\n\n          li {\n            &.dropdown {\n              .arrow {\n                opacity: 0;\n              }\n\n              &.open {\n                ul.dropdown-menu {\n                  display: none !important;\n                }\n              }\n            }\n          }\n        }\n      }\n\n      &:hover {\n        width: $offscreen-size;\n\n        .sidebar-inner {\n          .sidebar-logo {\n            border-bottom: 1px solid $border-color;\n            padding: 0 20px;\n          }\n\n          .sidebar-menu {\n            > li {\n              > a {\n                .title {\n                  display: inline-block;\n                }\n              }\n            }\n\n            li {\n              &.dropdown {\n                .arrow {\n                  opacity: 1;\n                }\n              }\n\n              &.open {\n                > ul.dropdown-menu {\n                  display: block !important;\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n\n    @include between($breakpoint-md, $breakpoint-xl) {\n      width: $offscreen-size;\n\n      .sidebar-inner {\n        .sidebar-logo {\n          border-bottom: 1px solid $border-color;\n          padding: 0 20px;\n\n          > a {\n            .logo {\n              background-position: center left;\n              width: 150px;\n            }\n          }\n        }\n\n        .sidebar-menu {\n          > li {\n            > a {\n              .title {\n                display: inline-block;\n              }\n            }\n          }\n\n          li {\n            &.dropdown {\n              .arrow {\n                opacity: 1;\n              }\n            }\n\n            &.open {\n              > ul.dropdown-menu {\n                display: block !important;\n              }\n            }\n          }\n        }\n      }\n    }\n\n    @include to($breakpoint-md) {\n      left: 0;\n    }\n  }\n}\n\n// Dark mode sidebar improvements - subtle, cohesive dark theme\n[data-theme=\"dark\"] {\n  .sidebar {\n    background-color: var(--c-bkg-card);\n\n    .sidebar-menu {\n      border-right-color: var(--c-border);\n\n      li {\n        a {\n          color: var(--c-text-muted);\n\n          .icon-holder {\n            color: var(--c-icon);\n          }\n\n          &:hover,\n          &:focus {\n            color: var(--c-text-base);\n            background-color: var(--c-bkg-hover);\n\n            .icon-holder {\n              color: var(--c-icon-hover);\n            }\n          }\n        }\n\n        &.dropdown {\n          &.open {\n            > a {\n              color: var(--c-text-base);\n              background-color: var(--c-bkg-hover);\n\n              .icon-holder {\n                color: var(--c-icon-hover);\n              }\n            }\n          }\n        }\n\n        // Active menu item styling for dark mode - subtle left border accent\n        &.actived {\n          > a {\n            color: var(--c-text-base) !important;\n            background-color: var(--c-bkg-hover) !important;\n            border-left: 3px solid var(--c-primary) !important;\n            margin-left: -3px;\n\n            .icon-holder {\n              color: var(--c-primary) !important;\n            }\n          }\n        }\n\n        // Styling for dropdown parent when it has an active child\n        &.dropdown.has-active-child {\n          > a {\n            color: var(--c-text-base) !important;\n            background-color: transparent !important;\n            border-left: 3px solid var(--c-primary) !important;\n            margin-left: -3px;\n\n            .icon-holder {\n              color: var(--c-primary) !important;\n            }\n          }\n        }\n      }\n\n      // Dropdown menu items\n      > li {\n        &.dropdown {\n          ul.dropdown-menu {\n            > li {\n              > a {\n                color: var(--c-text-muted);\n\n                &:hover,\n                &:focus {\n                  color: var(--c-text-base);\n                  background-color: var(--c-bkg-hover);\n                }\n              }\n\n              &.actived {\n                a {\n                  color: var(--c-primary) !important;\n                  background-color: transparent !important;\n                  font-weight: 500;\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .sidebar-logo {\n    border-bottom-color: var(--c-border);\n    border-right-color: var(--c-border);\n\n    a {\n      .logo-text {\n        color: var(--c-text-base);\n      }\n    }\n\n    .mobile-toggle {\n      a {\n        color: var(--c-icon);\n\n        &:hover {\n          color: var(--c-icon-hover);\n        }\n      }\n    }\n  }\n}\n\n// Light mode active states (ensure proper visibility)\n[data-theme=\"light\"], :root {\n  .sidebar {\n    .sidebar-menu {\n      li {\n        &.actived {\n          > a {\n            background-color: color-mix(in srgb, #ffffff 85%, var(--c-primary)) !important;\n            color: var(--c-primary) !important;\n\n            .icon-holder {\n              color: var(--c-primary) !important;\n              background-color: color-mix(in srgb, var(--c-primary) 10%, transparent) !important;\n            }\n          }\n        }\n\n        // Styling for dropdown parent when it has an active child (light mode)\n        &.dropdown.has-active-child {\n          > a {\n            background-color: color-mix(in srgb, #ffffff 90%, var(--c-primary)) !important;\n            color: var(--c-primary) !important;\n\n            .icon-holder {\n              color: var(--c-primary) !important;\n              background-color: color-mix(in srgb, var(--c-primary) 8%, transparent) !important;\n            }\n          }\n        }\n\n        a {\n          &:hover,\n          &:focus {\n            background-color: color-mix(in srgb, #ffffff 90%, var(--c-primary));\n            color: var(--c-primary);\n\n            .icon-holder {\n              color: var(--c-primary);\n              background-color: color-mix(in srgb, var(--c-primary) 8%, transparent);\n            }\n          }\n        }\n      }\n\n      // Dropdown items for light mode\n      > li {\n        &.dropdown {\n          ul.dropdown-menu {\n            > li {\n              &.actived {\n                a {\n                  color: var(--c-primary) !important;\n                  background-color: color-mix(in srgb, #ffffff 85%, var(--c-primary)) !important;\n                }\n              }\n\n              > a {\n                &:hover,\n                &:focus {\n                  background-color: color-mix(in srgb, #ffffff 90%, var(--c-primary));\n                  color: var(--c-primary);\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n","// ---------------------------------------------------------\n// @TOC\n// ---------------------------------------------------------\n\n// + @General Media Query\n// + @All Above Media Query\n// + @All Under Media Query\n// + @Between Two Devices Media Query\n\n// ---------------------------------------------------------\n// @General Media Query Mixin\n// ---------------------------------------------------------\n\n// Mixin used for custom rules that don't follow\n// any of the following premade media queries.\n\n@mixin mq($condition) {\n  @media #{$condition} {\n    @content;\n  }\n}\n\n// ---------------------------------------------------------\n// @All Above Media Query Mixin\n// -------------