n-800             : #4e342e,\n  brown-900             : #3e2723,\n  grey-50               : #fafafa,\n  grey-100              : #f5f5f5,\n  grey-200              : #eeeeee,\n  grey-300              : #e0e0e0,\n  grey-400              : #bdbdbd,\n  grey-500              : #9e9e9e,\n  grey-600              : #757575,\n  grey-700              : #616161,\n  grey-800              : #424242,\n  grey-900              : #212121,\n  blue-grey-50          : #eceff1,\n  blue-grey-100         : #cfd8dc,\n  blue-grey-200         : #b0bec5,\n  blue-grey-300         : #90a4ae,\n  blue-grey-400         : #78909c,\n  blue-grey-500         : #607d8b,\n  blue-grey-600         : #546e7a,\n  blue-grey-700         : #455a64,\n  blue-grey-800         : #37474f,\n  blue-grey-900         : #263238,\n);\n","@use 'sass:color';\n@use '../settings/baseColors' as *;\n@use '../settings/breakpoints' as *;\n@use '../tools/mixins/mediaQueriesRanges' as *;\n@use '../tools/mixins/clearfix' as *;\n@use '../tools/mixins/placeholder' as *;\n\n// ---------------------------------------------------------\n// @TOC\n\n// + @Topbar\n// + @Collapsed State\n// + @Mobile Responsive Fixes\n\n// ---------------------------------------------------------\n// @Topbar\n// ---------------------------------------------------------\n\n.header {\n  background-color: var(--c-bkg-card);\n  border-bottom: 1px solid var(--c-border);\n  box-shadow: var(--shadow-sm);\n  display: block;\n  margin-bottom: 0;\n  padding: 0;\n  position: fixed;\n  transition: all 0.2s ease;\n  width: calc(100% - #{$offscreen-size});\n  z-index: 800;\n\n  @include to($breakpoint-md) {\n    width: 100%;\n  }\n\n  @include between($breakpoint-md, $breakpoint-xl) {\n    width: calc(100% - #{$collapsed-size});\n  }\n\n  .header-container {\n    @include clearfix;\n\n    height: $header-height;\n\n    .nav-left,\n    .nav-right {\n      list-style: none;\n      margin-bottom: 0;\n      padding-left: 0;\n      position: relative;\n\n      > li {\n        float: left;\n\n        > a {\n          color: var(--c-icon);\n          display: block;\n          line-height: $header-height;\n          min-height: $header-height;\n          padding: 0 15px;\n          transition: all 0.2s ease-in-out;\n\n          i {\n            font-size: 17px;\n            color: var(--c-icon);\n          }\n\n          &:hover,\n          &:focus {\n            color: var(--c-icon-hover);\n            text-decoration: none;\n\n            i {\n              color: var(--c-icon-hover);\n            }\n          }\n\n          @include to($breakpoint-md) {\n            padding: 0 15px;\n          }\n        }\n      }\n\n      .notifications {\n        position: relative;\n\n        .counter {\n          background-color: $default-danger;\n          border-radius: 50px;\n          color: $default-white;\n          font-size: 10px;\n          line-height: 1;\n          padding: 3px 5.5px;\n          position: absolute;\n          right: 6px;\n          top: 12px;\n        }\n\n        .dropdown-menu {\n          min-width: 350px;\n          padding: 0;\n\n          @include to($breakpoint-sm) {\n            max-width: 300px;\n          }\n        }\n      }\n    }\n\n    .dropdown-menu {\n      // display: block;\n      margin: 0;\n      transform-origin: top right;\n      // transform: scale(0, 0);\n      transition: transform 0.15s ease-out;\n\n      .divider {\n        border-bottom: 1px solid $border-color;\n        height: 1px;\n        overflow: hidden;\n      }\n\n      > li {\n        > a {\n          transition: all 0.2s ease-out;\n        }\n      }\n    }\n\n    .show {\n      .dropdown-menu {\n        transform: scale(1, 1);\n      }\n    }\n\n    .nav-left {\n      float: left;\n      margin-left: 15px;\n    }\n\n    .nav-right {\n      float: right;\n\n      .dropdown-menu {\n        left: auto;\n        right: 0;\n\n        > li {\n          width: 100%;\n\n          > a {\n            line-height: 1.5;\n            min-height: auto;\n            padding: 10px 15px;\n          }\n        }\n      }\n    }\n  }\n\n  .search-box {\n    .search-icon-close {\n      display: none;\n    }\n\n    &.active {\n      .search-icon {\n        display: none;\n      }\n\n      .search-icon-close {\n        display: inline-block;\n      }\n    }\n  }\n\n  .search-input {\n    display: none;\n\n    &.active {\n      display: inline-block;\n    }\n\n    input {\n      background-color: transparent;\n      border: 0;\n      box-shadow: none;\n      font-size: 18px;\n      height: 40px;\n      margin-top: 12px;\n      outline: none;\n      padding: 5px;\n\n      @include to($breakpoint-sm) {\n        width: 85px;\n      }\n\n      @include placeholder {\n        color: color.adjust($default-text-color, $lightness: 20%);\n        font-style: italic;\n      }\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @Mobile Responsive Fixes - AGGRESSIVE APPROACH\n// ---------------------------------------------------------\n\n// Tablet mobile fixes (768px to 991px)\n@include to($breakpoint-md) {\n  .header {\n    .header-container {\n      padding: 0 10px;\n      \n      .nav-left {\n        margin-left: 5px;\n\n        > li > a {\n          padding: 0 8px !important;\n        }\n      }\n\n      .nav-right {\n        margin-right: 5px;\n\n        > li {\n          > a {\n            padding: 0 8px !important;\n            \n            // Hide text in user dropdown on tablet\n            .peer:last-child {\n              display: none;\n            }\n          }\n        }\n\n        // Make theme toggle more compact\n        .theme-toggle {\n          padding: 0 5px !important;\n          \n          .form-check-label {\n            font-size: 9px !important;\n            margin: 0 3px !important;\n          }\n          \n          .form-check-input {\n            width: 1.8rem !important;\n            height: 1rem !important;\n          }\n        }\n      }\n    }\n  }\n}\n\n// Small mobile phones (576px to 767px)\n@include to($breakpoint-sm) {\n  .header {\n    .header-container {\n      height: auto;\n      min-height: $header-height;\n      padding: 0 5px;\n\n      .nav-left {\n        margin-left: 2px;\n\n        > li > a {\n          padding: 0 5px !important;\n        }\n\n        // Hide search toggle on small mobile - make it icon only when active\n        .search-box:not(.active) {\n          display: none;\n        }\n      }\n\n      .nav-right {\n        margin-right: 2px;\n\n        > li {\n          > a {\n            padding: 0 4px !important;\n            \n            // Hide all text content, keep only icons\n            span {\n              display: none;\n            }\n            \n            .peer:last-child {\n              display: none;\n            }\n          }\n        }\n\n        // Ultra-compact theme toggle\n        .theme-toggle {\n          padding: 0 3px !important;\n          \n          .form-check {\n            flex-direction: column;\n            align-items: center;\n            \n            .form-check-label {\n              font-size: 7px !important;\n              margin: 0 !important;\n              line-height: 1 !important;\n              \n              span {\n                display: none !important;\n              }\n            }\n            \n            .form-check-input {\n              width: 1.5rem !important;\n              height: 0.8rem !important;\n              margin: 1px 0 !important;\n            }\n          }\n        }\n      }\n\n      // Full-screen mobile dropdowns\n      .nav-right .dropdown-menu {\n        position: fixed !important;\n        top: $header-height !important;\n        left: 0 !important;\n        right: 0 !important;\n        bottom: 0 !important;\n        width: 100vw !important;\n        height: calc(100vh - #{$header-height}) !important;\n        max-width: none !important;\n        min-width: auto !important;\n        transform: none !important;\n        border-radius: 0 !important;\n        z-index: 9999;\n        overflow-y: auto;\n        \n        // Close button for mobile dropdowns\n        &::before {\n          content: \" Close\";\n          position: sticky;\n          top: 0;\n          display: block;\n          background: var(--c-primary);\n          color: white;\n          text-align: center;\n          padding: 10px;\n          cursor: pointer;\n          font-weight: bold;\n          z-index: 10000;\n        }\n      }\n    }\n  }\n}\n\n// Extra small mobile phones (less than 576px)\n@include to(480px) {\n  .header {\n    .header-container {\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      flex-wrap: nowrap;\n      padding: 0 2px;\n\n      .nav-left {\n        flex: 0 0 auto;\n        margin: 0;\n        \n        > li {\n          float: none;\n          display: inline-block;\n          \n          > a {\n            padding: 0 3px !important;\n            font-size: 14px !important;\n          }\n        }\n\n        // Search overlay for tiny screens\n        .search-input.active {\n          position: fixed;\n          top: $header-height;\n          left: 0;\n          right: 0;\n          background: var(--c-bkg-card);\n          border-top: 1px solid var(--c-border);\n          padding: 15px;\n          z-index: 9998;\n          \n          input {\n            width: 100%;\n            padding: 12px;\n            font-size: 16px;\n            border: 1px solid var(--c-border);\n            border-radius: 6px;\n            background: var(--c-bkg-card);\n            color: var(--c-text-base);\n            margin-top: 0;\n            \n            &::placeholder {\n              color: var(--c-text-muted);\n            }\n          }\n        }\n      }\n\n      .nav-right {\n        flex: 0 0 auto;\n        margin: 0;\n        \n        > li {\n          float: none;\n          display: inline-block;\n          \n          > a {\n            padding: 0 2px !important;\n            font-size: 12px !important;\n          }\n        }\n\n        // Minimal theme toggle\n        .theme-toggle {\n          padding: 0 1px !important;\n          \n          .form-check {\n            .form-check-label {\n              display: none !important;\n            }\n            \n            .form-check-input {\n              width: 1.2rem !important;\n              height: 0.7rem !important;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @Collapsed State\n// ---------------------------------------------------------\n\n.is-collapsed {\n  .header {\n    width: calc(100% - #{$collapsed-size});\n\n    @include to($breakpoint-md) {\n      width: 100%;\n    }\n\n    @include between($breakpoint-md, $breakpoint-xl) {\n      width: calc(100% - #{$offscreen-size});\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @Dark Mode Topbar\n// ---------------------------------------------------------\n\n[data-theme=\"dark\"] {\n  .header {\n    background-color: var(--c-bkg-card);\n    border-bottom-color: var(--c-border);\n\n    .header-container {\n      .nav-left,\n      .nav-right {\n        > li {\n          > a {\n            color: var(--c-icon);\n\n            i {\n              color: var(--c-icon);\n            }\n\n            &:hover,\n            &:focus {\n              color: var(--c-icon-hover);\n\n              i {\n                color: var(--c-icon-hover);\n              }\n            }\n\n            // Active state for hamburger/search when clicked\n            &:active,\n            &.active {\n              color: var(--c-text-base);\n\n              i {\n                color: var(--c-text-base);\n              }\n            }\n          }\n        }\n      }\n\n      // Search box active state\n      .search-box {\n        &.active {\n          a {\n            color: var(--c-icon-hover);\n\n            i {\n              color: var(--c-icon-hover);\n            }\n          }\n        }\n      }\n\n      // Dropdown menu dark mode\n      .dropdown-menu {\n        background-color: var(--c-bkg-card);\n        border-color: var(--c-border);\n\n        .divider {\n          border-bottom-color: var(--c-border);\n        }\n\n        > li > a {\n          color: var(--c-text-muted);\n\n          &:hover,\n          &:focus {\n            color: var(--c-text-base);\n            background-color: var(--c-bkg-hover);\n          }\n        }\n      }\n    }\n\n    // Search input dark mode\n    .search-input {\n      input {\n        color: var(--c-text-base);\n\n        &::placeholder {\n          color: var(--c-text-muted);\n        }\n      }\n    }\n  }\n}\n","//----------------------------------------------------------\n// @Placeholder\n//----------------------------------------------------------\n\n@mixin placeholder {\n  &::-webkit-input-placeholder { @content; }\n  &:-moz-placeholder { @content; }\n  &::-moz-placeholder { @content; }\n  &:-ms-input-placeholder { @content; }\n}\n","@use '../settings/baseColors' as *;\n@use '../settings/breakpoints' as *;\n@use '../tools/mixins/mediaQueriesRanges' as *;\n\n// ---------------------------------------------------------\n// @TOC\n// ---------------------------------------------------------\n\n// + @Page Container\n// + @Main Content\n// + @Full Container\n// + @Collapsed State\n// + @Mobile Layout Fixes\n\n// ---------------------------------------------------------\n// @Page Container - MODERN LAYOUT APPROACH\n// ---------------------------------------------------------\n\n.page-container {\n  min-height: 100vh;\n  padding-left: $offscreen-size;\n  transition: all 0.2s ease;\n  \n  // Modern flexbox layout to prevent footer overlap\n  display: flex;\n  flex-direction: column;\n\n  @include between($breakpoint-md, $breakpoint-xl) {\n    padding-left: $collapsed-size;\n  }\n\n  @include to($breakpoint-md) {\n    padding-left: 0;\n  }\n}\n\n// ---------------------------------------------------------\n// @Main Content - FLEXIBLE LAYOUT\n// ---------------------------------------------------------\n\n.main-content {\n  padding: 85px 20px 20px;\n  \n  // Flex-grow to push footer to bottom\n  flex: 1 0 auto;\n  min-height: 0; // Allow flex shrinking\n  \n  // Ensure content doesn't overflow\n  overflow-x: hidden;\n\n  @include to($breakpoint-md) {\n    padding: 85px 5px 5px;\n  }\n\n  @include to($breakpoint-sm) {\n    padding: 85px 10px 30px; // Extra bottom padding on mobile\n  }\n  \n  @include to(400px) {\n    padding: 85px 5px 40px; // Even more bottom padding on tiny screens\n  }\n}\n\n.remain-height {\n  height: calc(100vh - 126px);\n}\n\n// ---------------------------------------------------------\n// @Full Container\n// ---------------------------------------------------------\n\n.full-container {\n  left: $offscreen-size;\n  min-height: calc(100vh - #{$header-height});\n  position: absolute;\n  right: 0;\n  top: $header-height;\n  transition: all 0.2s ease;\n\n  @include between($breakpoint-md, $breakpoint-xl) {\n    left: 0;\n    padding-left: $collapsed-size;\n  }\n\n  @include to($breakpoint-md) {\n    left: 0;\n  }\n}\n\n// ---------------------------------------------------------\n// @Mobile Layout Fixes - AGGRESSIVE FOOTER SOLUTION\n// ---------------------------------------------------------\n\n// Footer - completely redesigned for mobile\nfooter {\n  // Flex-shrink: 0 to prevent compression\n  flex: 0 0 auto;\n  \n  // Positioning\n  position: relative;\n  z-index: 1;\n  \n  // Styling\n  margin-top: auto;\n  padding: 20px;\n  text-align: center;\n  border-top: 1px solid var(--c-border);\n  background: var(--c-bkg-card);\n  color: var(--c-text-muted);\n  font-size: 12px;\n  line-height: 1.4;\n  \n  // Prevent any potential overflow\n  word-wrap: break-word;\n  overflow-wrap: break-word;\n\n  // Tablet footer adjustments\n  @include to($breakpoint-md) {\n    padding: 18px 15px;\n    font-size: 11px;\n  }\n\n  // Mobile footer adjustments\n  @include to($breakpoint-sm) {\n    padding: 15px 10px;\n    font-size: 10px;\n    line-height: 1.3;\n    \n    span {\n      display: inline-block;\n      max-width: 100%;\n      \n      a {\n        color: var(--c-primary);\n        text-decoration: none;\n        word-break: break-word;\n        \n        &:hover {\n          text-decoration: underline;\n        }\n      }\n    }\n  }\n\n  // Tiny screen footer adjustments\n  @include to(400px) {\n    padding: 12px 8px;\n    font-size: 9px;\n    line-height: 1.2;\n    \n    span {\n      display: block;\n      \n      // Break long text on new lines for readability\n      &::after {\n        content: \"\";\n        display: block;\n        height: 2px;\n      }\n    }\n  }\n}\n\n// Ensure body and html take full height for flex layout\nhtml, body {\n  height: 100%;\n  margin: 0;\n  padding: 0;\n}\n\n// Global mobile overflow prevention\n@include to($breakpoint-sm) {\n  body {\n    overflow-x: hidden;\n  }\n  \n  // Prevent any element from causing horizontal scroll\n  * {\n    max-width: 100%;\n    box-sizing: border-box;\n  }\n}\n\n// Additional mobile content spacing\n@include to($breakpoint-sm) {\n  .page-container {\n    .main-content {\n      // Extra margin-bottom to ensure footer never overlaps\n      margin-bottom: 20px;\n      \n      // Responsive content adjustments\n      .row {\n        margin-left: 0;\n        margin-right: 0;\n      }\n      \n      .col-md-6, .col-md-3, .col-md-12 {\n        padding-left: 5px;\n        padding-right: 5px;\n      }\n    }\n  }\n}\n\n// Emergency footer overlap prevention\n@include to(480px) {\n  .page-container {\n    // Force minimum height that accounts for content\n    min-height: calc(100vh - 80px);\n    \n    .main-content {\n      // Ensure there's always space for footer\n      padding-bottom: 60px !important;\n      margin-bottom: 20px !important;\n    }\n  }\n  \n  footer {\n    // Stick to bottom on very small screens\n    position: relative;\n    margin-top: auto;\n    clear: both;\n  }\n}\n\n// ---------------------------------------------------------\n// @Collapsed State\n// ---------------------------------------------------------\n\n.is-collapsed {\n  .page-container {\n    padding-left: $collapsed-size;\n\n    @include to($breakpoint-md) {\n      padding-left: 0;\n    }\n\n    @include between($breakpoint-md, $breakpoint-xl) {\n      padding-left: $offscreen-size;\n    }\n  }\n\n  .full-container {\n    left: $collapsed-size;\n\n    @include to($breakpoint-md) {\n      left: 0;\n    }\n\n    @include between($breakpoint-md, $breakpoint-xl) {\n      left: $offscreen-size;\n      padding-left: 0;\n    }\n  }\n}\n",".progress {\n  height: 4px;\n  background-color: var(--c-border);\n  border-radius: 4px;\n  margin-bottom: 10px;\n}\n",".easy-pie-chart {\n  position: relative;\n\n  span {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    line-height: 0;\n  }\n}\n","@use '../settings/baseColors' as *;\n@use '../settings/materialColors' as *;\n\n.checkbox label {\n  display: inline-block;\n  vertical-align: middle;\n  position: relative;\n  padding-left: 17px;\n  margin-bottom: 0;\n}\n\n.checkbox label::before {\n  content: \"\";\n  display: inline-block;\n  position: absolute;\n  width: 17px;\n  height: 17px;\n  left: 0;\n  top: 50%;\n  transform: translateY(-50%);\n  margin-left: -12px;\n  border: 1px solid $grey-300;\n  border-radius: 3px;\n  background-color: $md-white;\n  -webkit-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;\n  -o-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;\n  transition: border 0.15s ease-in-out, color 0.15s ease-in-out;\n}\n\n.checkbox label::after {\n  display: inline-block;\n  position: absolute;\n  width: 17px;\n  height: 17px;\n  text-align: center;\n  font-size: 10px !important;\n  line-height: 17px;\n  left