.radio {\n  padding-left: 20px;\n}\n\n.radio label {\n  display: inline-block;\n  vertical-align: middle;\n  position: relative;\n  padding-left: 5px;\n}\n\n.radio label::before {\n  content: \"\";\n  display: inline-block;\n  position: absolute;\n  width: 17px;\n  height: 17px;\n  left: 0;\n  margin-left: -20px;\n  border: 1px solid #cccccc;\n  border-radius: 50%;\n  background-color: var(--c-bkg-card);\n  -webkit-transition: border 0.15s ease-in-out;\n  -o-transition: border 0.15s ease-in-out;\n  transition: border 0.15s ease-in-out;\n}\n\n.radio label::after {\n  display: inline-block;\n  position: absolute;\n  content: \" \";\n  width: 11px;\n  height: 11px;\n  left: 3px;\n  top: 3px;\n  margin-left: -20px;\n  border-radius: 50%;\n  background-color: var(--c-text-base);\n  -webkit-transform: scale(0, 0);\n  -ms-transform: scale(0, 0);\n  -o-transform: scale(0, 0);\n  transform: scale(0, 0);\n  -webkit-transition: -webkit-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);\n  -moz-transition: -moz-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);\n  -o-transition: -o-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);\n  transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);\n}\n\n.radio input[type=\"radio\"] {\n  opacity: 0;\n  z-index: 1;\n}\n\n.radio input[type=\"radio\"]:focus + label::before {\n  outline: thin dotted;\n  outline: 5px auto -webkit-focus-ring-color;\n  outline-offset: -2px;\n}\n\n.radio input[type=\"radio\"]:checked + label::after {\n  -webkit-transform: scale(1, 1);\n  -ms-transform: scale(1, 1);\n  -o-transform: scale(1, 1);\n  transform: scale(1, 1);\n}\n\n.radio input[type=\"radio\"]:disabled + label {\n  opacity: 0.65;\n}\n\n.radio input[type=\"radio\"]:disabled + label::before {\n  cursor: not-allowed;\n}\n\n.radio.radio-inline {\n  margin-top: 0;\n}\n\n.radio-primary input[type=\"radio\"] + label::after {\n  background-color: #428bca;\n}\n\n.radio-primary input[type=\"radio\"]:checked + label::before {\n  border-color: #428bca;\n}\n\n.radio-primary input[type=\"radio\"]:checked + label::after {\n  background-color: #428bca;\n}\n\n.radio-danger input[type=\"radio\"] + label::after {\n  background-color: #d9534f;\n}\n\n.radio-danger input[type=\"radio\"]:checked + label::before {\n  border-color: #d9534f;\n}\n\n.radio-danger input[type=\"radio\"]:checked + label::after {\n  background-color: #d9534f;\n}\n\n.radio-info input[type=\"radio\"] + label::after {\n  background-color: #5bc0de;\n}\n\n.radio-info input[type=\"radio\"]:checked + label::before {\n  border-color: #5bc0de;\n}\n\n.radio-info input[type=\"radio\"]:checked + label::after {\n  background-color: #5bc0de;\n}\n\n.radio-warning input[type=\"radio\"] + label::after {\n  background-color: #f0ad4e;\n}\n\n.radio-warning input[type=\"radio\"]:checked + label::before {\n  border-color: #f0ad4e;\n}\n\n.radio-warning input[type=\"radio\"]:checked + label::after {\n  background-color: #f0ad4e;\n}\n\n.radio-success input[type=\"radio\"] + label::after {\n  background-color: #5cb85c;\n}\n\n.radio-success input[type=\"radio\"]:checked + label::before {\n  border-color: #5cb85c;\n}\n\n.radio-success input[type=\"radio\"]:checked + label::after {\n  background-color: #5cb85c;\n}\n\n// Dark mode specific fixes for birthdate field only\n[data-theme=\"dark\"] {\n  // Date picker specific styling (birthdate field)\n  .timepicker-input {\n    .input-group-text {\n      background-color: var(--c-bkg-card) !important;\n      border-color: var(--c-border) !important;\n      color: var(--c-text-base) !important;\n\n      // Override specific background classes\n      &.bgc-white {\n        background-color: var(--c-bkg-card) !important;\n      }\n\n      // Calendar icon styling\n      .ti-calendar {\n        color: var(--c-text-base) !important;\n      }\n    }\n\n    .form-control {\n      &.bdc-grey-200 {\n        border-color: var(--c-border) !important;\n      }\n    }\n  }\n}\n","#loader {\n  transition: all 0.3s ease-in-out;\n  opacity: 1;\n  visibility: visible;\n  position: fixed;\n  height: 100vh;\n  width: 100%;\n  background: var(--loader-bg);\n  z-index: 90000;\n}\n\n#loader.fadeOut {\n  opacity: 0;\n  visibility: hidden;\n}\n\n.spinner {\n  width: 40px;\n  height: 40px;\n  position: absolute;\n  top: calc(50% - 20px);\n  left: calc(50% - 20px);\n  background-color: var(--spinner-bg);\n  border-radius: 100%;\n  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;\n  animation: sk-scaleout 1.0s infinite ease-in-out;\n}\n\n@-webkit-keyframes sk-scaleout {\n  0% { -webkit-transform: scale(0) }\n  100% {\n    -webkit-transform: scale(1.0);\n    opacity: 0;\n  }\n}\n\n@keyframes sk-scaleout {\n  0% {\n    -webkit-transform: scale(0);\n    transform: scale(0);\n  } 100% {\n    -webkit-transform: scale(1.0);\n    transform: scale(1.0);\n    opacity: 0;\n  }\n}\n","footer {\n  z-index: 1;\n  position: relative;\n}\n","// ---------------------------------------------------------\n// @Modern Design Enhancements\n// Subtle improvements for a contemporary look\n// ---------------------------------------------------------\n\n// ---------------------------------------------------------\n// @Body & Background\n// ---------------------------------------------------------\n\nbody {\n  background-color: var(--c-bkg-body);\n  // Subtle dot pattern for depth\n  background-image: radial-gradient(circle at 1px 1px, var(--c-border) 1px, transparent 0);\n  background-size: 24px 24px;\n}\n\n// ---------------------------------------------------------\n// @Cards - Modern styling\n// ---------------------------------------------------------\n\n.card {\n  background: var(--c-bkg-card);\n  border: 1px solid var(--c-border);\n  border-radius: var(--radius-md);\n  box-shadow: var(--shadow-card);\n  transition: box-shadow 0.2s ease, transform 0.2s ease;\n\n  &:hover {\n    box-shadow: var(--shadow-md);\n  }\n}\n\n.card-header {\n  background: transparent;\n  border-bottom: 1px solid var(--c-border-light);\n  padding: 1rem 1.25rem;\n  font-weight: 600;\n  color: var(--c-text-base);\n}\n\n.card-body {\n  padding: 1.25rem;\n}\n\n.card-footer {\n  background: var(--c-bkg-hover);\n  border-top: 1px solid var(--c-border-light);\n  padding: 0.875rem 1.25rem;\n}\n\n// ---------------------------------------------------------\n// @Stat Cards - Dashboard widgets\n// ---------------------------------------------------------\n\n.peers {\n  &.ai-c {\n    .peer {\n      &:first-child {\n        // Icon container styling\n        .layers {\n          border-radius: var(--radius-md);\n          transition: transform 0.2s ease;\n\n          &:hover {\n            transform: scale(1.05);\n          }\n        }\n      }\n    }\n  }\n}\n\n// Stat value styling\n.fw-500 {\n  font-weight: 600;\n  letter-spacing: -0.02em;\n}\n\n// ---------------------------------------------------------\n// @Tables - Modern look\n// ---------------------------------------------------------\n\n.table {\n  --bs-table-bg: var(--c-bkg-card);\n\n  thead {\n    th {\n      background: var(--c-bkg-hover);\n      border-bottom: 2px solid var(--c-border);\n      color: var(--c-text-base);\n      font-weight: 600;\n      font-size: 0.75rem;\n      text-transform: uppercase;\n      letter-spacing: 0.05em;\n      padding: 0.875rem 1rem;\n    }\n  }\n\n  tbody {\n    tr {\n      transition: background-color 0.15s ease;\n\n      &:hover {\n        background-color: var(--c-bkg-hover);\n      }\n\n      td {\n        padding: 0.875rem 1rem;\n        border-bottom: 1px solid var(--c-border-light);\n        vertical-align: middle;\n      }\n    }\n  }\n}\n\n.table-bordered {\n  border: 1px solid var(--c-border);\n  border-radius: var(--radius-md);\n  overflow: hidden;\n\n  th,\n  td {\n    border-color: var(--c-border-light);\n  }\n}\n\n// ---------------------------------------------------------\n// @Buttons - Refined styling\n// ---------------------------------------------------------\n\n.btn {\n  border-radius: var(--radius-sm);\n  font-weight: 500;\n  padding: 0.5rem 1rem;\n  transition: all 0.2s ease;\n  border: none;\n\n  &:focus {\n    box-shadow: 0 0 0 3px rgb(99 102 241 / 0.25);\n  }\n}\n\n.btn-primary {\n  background: linear-gradient(135deg, var(--c-primary) 0%, var(--c-primary-dark) 100%);\n\n  &:hover {\n    background: linear-gradient(135deg, var(--c-primary-light) 0%, var(--c-primary) 100%);\n    transform: translateY(-1px);\n    box-shadow: var(--shadow-md);\n  }\n\n  &:active {\n    transform: translateY(0);\n  }\n}\n\n.btn-success {\n  background: linear-gradient(135deg, var(--c-success) 0%, #059669 100%);\n\n  &:hover {\n    transform: translateY(-1px);\n    box-shadow: var(--shadow-md);\n  }\n}\n\n.btn-danger {\n  background: linear-gradient(135deg, var(--c-danger) 0%, #dc2626 100%);\n\n  &:hover {\n    transform: translateY(-1px);\n    box-shadow: var(--shadow-md);\n  }\n}\n\n.btn-outline-primary {\n  border: 1.5px solid var(--c-primary);\n  color: var(--c-primary);\n  background: transparent;\n\n  &:hover {\n    background: var(--c-primary);\n    color: white;\n    transform: translateY(-1px);\n  }\n}\n\n// ---------------------------------------------------------\n// @Form Controls - Modern inputs\n// ---------------------------------------------------------\n\n.form-control,\n.form-select {\n  background-color: var(--c-bkg-card);\n  border: 1.5px solid var(--c-border);\n  border-radius: var(--radius-sm);\n  color: var(--c-text-base);\n  padding: 0.625rem 0.875rem;\n  transition: border-color 0.2s ease, box-shadow 0.2s ease;\n\n  &:focus {\n    border-color: var(--c-primary);\n    box-shadow: 0 0 0 3px rgb(99 102 241 / 0.15);\n    outline: none;\n  }\n\n  &::placeholder {\n    color: var(--c-text-light);\n  }\n}\n\n.form-label {\n  color: var(--c-text-base);\n  font-weight: 500;\n  font-size: 0.875rem;\n  margin-bottom: 0.5rem;\n}\n\n// ---------------------------------------------------------\n// @Badges - Modern pills\n// ---------------------------------------------------------\n\n.badge {\n  border-radius: 50px;\n  font-weight: 500;\n  font-size: 0.75rem;\n  padding: 0.35em 0.75em;\n  letter-spacing: 0.02em;\n}\n\n.bg-primary {\n  background: linear-gradient(135deg, var(--c-primary) 0%, var(--c-primary-dark) 100%) !important;\n}\n\n.bg-success {\n  background: linear-gradient(135deg, var(--c-success) 0%, #059669 100%) !important;\n}\n\n.bg-danger {\n  background: linear-gradient(135deg, var(--c-danger) 0%, #dc2626 100%) !important;\n}\n\n.bg-warning {\n  background: linear-gradient(135deg, var(--c-warning) 0%, #d97706 100%) !important;\n  color: #1e293b !important;\n}\n\n.bg-info {\n  background: linear-gradient(135deg, var(--c-info) 0%, #0284c7 100%) !important;\n}\n\n// ---------------------------------------------------------\n// @Dropdowns - Refined menus\n// ---------------------------------------------------------\n\n.dropdown-menu {\n  background: var(--c-bkg-card);\n  border: 1px solid var(--c-border);\n  border-radius: var(--radius-md);\n  box-shadow: var(--shadow-lg);\n  padding: 0.5rem;\n  margin-top: 0.5rem;\n\n  .dropdown-item {\n    border-radius: var(--radius-sm);\n    color: var(--c-text-base);\n    padding: 0.5rem 0.875rem;\n    transition: background-color 0.15s ease;\n\n    &:hover,\n    &:focus {\n      background-color: var(--c-bkg-hover);\n      color: var(--c-primary);\n    }\n\n    &.active,\n    &:active {\n      background-color: var(--c-primary);\n      color: white;\n    }\n  }\n\n  .dropdown-divider {\n    border-top-color: var(--c-border-light);\n    margin: 0.5rem 0;\n  }\n}\n\n// ---------------------------------------------------------\n// @Progress Bars - Modern look\n// ---------------------------------------------------------\n\n.progress {\n  background-color: var(--c-border-light);\n  border-radius: 50px;\n  height: 8px;\n  overflow: hidden;\n}\n\n.progress-bar {\n  border-radius: 50px;\n  background: linear-gradient(90deg, var(--c-primary) 0%, var(--c-primary-light) 100%);\n}\n\n// ---------------------------------------------------------\n// @Alerts - Soft backgrounds\n// ---------------------------------------------------------\n\n.alert {\n  border: none;\n  border-radius: var(--radius-md);\n  border-left: 4px solid;\n}\n\n.alert-primary {\n  background-color: rgb(99 102 241 / 0.1);\n  border-left-color: var(--c-primary);\n  color: var(--c-primary-dark);\n}\n\n.alert-success {\n  background-color: rgb(16 185 129 / 0.1);\n  border-left-color: var(--c-success);\n  color: #047857;\n}\n\n.alert-danger {\n  background-color: rgb(239 68 68 / 0.1);\n  border-left-color: var(--c-danger);\n  color: #b91c1c;\n}\n\n.alert-warning {\n  background-color: rgb(245 158 11 / 0.1);\n  border-left-color: var(--c-warning);\n  color: #92400e;\n}\n\n.alert-info {\n  background-color: rgb(14 165 233 / 0.1);\n  border-left-color: var(--c-info);\n  color: #0369a1;\n}\n\n// ---------------------------------------------------------\n// @Nav Tabs - Modern tabs\n// ---------------------------------------------------------\n\n.nav-tabs {\n  border-bottom: 2px solid var(--c-border-light);\n  gap: 0.25rem;\n\n  .nav-link {\n    border: none;\n    border-radius: var(--radius-sm) var(--radius-sm) 0 0;\n    color: var(--c-text-muted);\n    font-weight: 500;\n    padding: 0.75rem 1.25rem;\n    transition: all 0.2s ease;\n    position: relative;\n\n    &:hover {\n      color: var(--c-primary);\n      background-color: var(--c-bkg-hover);\n    }\n\n    &.active {\n      color: var(--c-primary);\n      background-color: var(--c-bkg-card);\n\n      &::after {\n        content: '';\n        position: absolute;\n        bottom: -2px;\n        left: 0;\n        right: 0;\n        height: 2px;\n        background: var(--c-primary);\n      }\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @Pagination - Modern style\n// ---------------------------------------------------------\n\n.pagination {\n  gap: 0.25rem;\n\n  .page-link {\n    border: none;\n    border-radius: var(--radius-sm);\n    color: var(--c-text-base);\n    font-weight: 500;\n    min-width: 36px;\n    height: 36px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: all 0.2s ease;\n\n    &:hover {\n      background-color: var(--c-bkg-hover);\n      color: var(--c-primary);\n    }\n  }\n\n  .page-item {\n    &.active .page-link {\n      background: linear-gradient(135deg, var(--c-primary) 0%, var(--c-primary-dark) 100%);\n      color: white;\n    }\n\n    &.disabled .page-link {\n      color: var(--c-text-light);\n      background: transparent;\n    }\n  }\n}\n\n// ---------------------------------------------------------\n// @List Groups - Refined lists\n// ---------------------------------------------------------\n\n.list-group {\n  border-radius: var(--radius-md);\n  overflow: hidden;\n}\n\n.list-group-item {\n  background-color: var(--c-bkg-card);\n  border: none;\n  border-bottom: 1px solid var(--c-border-light);\n  padding: 1rem 1.25rem;\n  transition: background-color 0.15s ease;\n\n  &:last-child {\n    border-bottom: none;\n  }\n\n  &:hover {\n    background-color: var(--c-bkg-hover);\n  }\n\n  &.active {\n    background: linear-gradient(135deg, var(--c-primary) 0%, var(--c-primary-dark) 100%);\n    border-color: transparent;\n    color: white;\n  }\n}\n\n// ---------------------------------------------------------\n// @Tooltips & Popovers\n// ---------------------------------------------------------\n\n.tooltip-inner {\n  background-color: var(--c-text-base);\n  border-radius: var(--radius-sm);\n  font-size: 0.8125rem;\n  padding: 0.5rem 0.75rem;\n}\n\n.popover {\n  border: 1px solid var(--c-border);\n  border-radius: var(--radius-md);\n  box-shadow: var(--shadow-lg);\n}\n\n// ---------------------------------------------------------\n// @Modals - Modern dialogs\n// ---------------------------------------------------------\n\n.modal-content {\n  background: var(--c-bkg-card);\n  border: 1px solid var(--c-border);